import Look from '../../../components/look.vue'
export default {
	components: {
		Look
	},
	data() {
		return {
			title: '职业技能培训',
			list:[]
		}
	},
	created() {
		this.getData()
	},
	methods: {
		getData() {
			var data = {
				category: "劳动竞赛信息发布", 
				channel: "职业技能培训"
			}
			this.$axios(this.$api.getList , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.list = res.data.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
	}
}



